
import React, { useState, useEffect } from 'react'
import down from './img/down.svg'
import go from './img/go.svg'
import check from './img/check.svg'
import discussion from './img/governance-discussion.svg'
import ApiProxy from '../../model/ApiProxy'
import pollVotes from './img/pool-votes.svg'
import executive from './img/executive-votes.svg'
import apiProxy from '../../model/ApiProxy'
import './governance.less'
import { Link } from 'react-router-dom'
export default function Governance({ lang, getLang }) {
  const [inProgress, setInProgress] = useState(true)
  const [executed, setExecuted] = useState(true)
  const [sumPower, setSumPower] = useState(0)
  const [winner, setWinner] = useState(0)
  const [winnerOption, setWinnerOption] = useState()
  const [activePollVotes, setActivePollVotes] = useState('')
  const scrollToAnchor = (anchorName) => {
    if (anchorName) {
      let anchorElement = document.getElementById(anchorName);
      if (anchorElement) {
        anchorElement.scrollIntoView()
      }
    }
  }
  const getProposals = async () => {
    let res = await apiProxy.request('getProposals', [])
    if (res) {
      setActivePollVotes(res.count)
    }
  }
  const getVotingResult = async () => {
    let res = await ApiProxy.request("getVotingResult")
    let sum = (+res.option_1) + (+res.option_2)
    console.log("getVotingResult", res)
    let winnerPower = (+res.option_1) > (+res.option_2) ? res.option_1 : res.option_2
    let winnerOp = (+res.option_1) > (+res.option_2) ? "1" : "2"
    setSumPower(sum)
    setWinnerOption(winnerOp)
    setWinner(winnerPower)
  }

  useEffect(() => {
    getProposals()
    getVotingResult()
  }, [])
  return (
    <div className='governance-box'>
      <div className='governance-box-info'>
        <div className='governance-title'>
          {lang['title']}
        </div>
        <div className='governance-des'>
          {lang['governance-des']}
        </div>
        <a rel='noreferrer' target="_blank" href='https://discord.gg/kb8ZbYgp8M'>
          <div className='active-pool-votes-num'>
            <div className={activePollVotes ? 'active-num' : 'no-active-num'}>{activePollVotes}</div> {lang['active-pool-votes']}
          </div>
        </a>
        <div className='active-esectutive-votes-num' onClick={() => { scrollToAnchor("EXECUTIVEVOTES") }}>
          <div className='active-num'>1</div>
          {lang['active-esectutive-votes']}
        </div>
        <div className='down'>
          <img src={down} alt='down' />
        </div>
        <div className='how-to-participate'>
          {lang['how-to-participate']}
        </div>
        <div className='participate-step'>
          <a rel='noreferrer' target="_blank" href='https://discord.gg/kb8ZbYgp8M'>
            <div className='step'>
              <div className='step-one-info'>
                <div className='hover-go'>
                  <img src={go} alt='go' />
                </div>
                <div className='step-logo'>
                  <img src={discussion} alt='discussion' />
                </div>
                <div className='step-text'>
                  {lang['governance-discussion']}
                </div>
              </div>
              <div className='hover'>
                <div className='san'></div>
                {lang['governance-discussion-hover']}
              </div>
            </div>
          </a>
          <a rel='noreferrer' target="_blank" href='https://snapshot.org/#/deri.eth'>
            <div className='step'>
              <div className='step-one-info'>
                <div className='hover-go'>
                  <img src={go} alt='go' />
                </div>
                <div className='step-logo'>
                  <img src={pollVotes} alt='discussion' />
                </div>
                <div className='step-text'>
                  {lang['poll-votes']}
                </div>
              </div>
              <div className='hover'>
                <div className='san'></div>
                {lang['poll-votes-hover']}
              </div>
            </div>
          </a>

          <div className='step' onClick={() => { scrollToAnchor("EXECUTIVEVOTES") }}>
            <div className='step-one-info'>
              <div className='hover-go'>
              </div>
              <div className='step-logo'>
                <img src={executive} alt='discussion' />
              </div>
              <div className='step-text'>
                {lang['executive-votes']}
              </div>
            </div>
            <div className='hover'>
              <div className='san'></div>
              {lang['executive-votes-hover']}
            </div>
          </div>
        </div>
        <div id='EXECUTIVEVOTES' className='executive-votes'>
          {lang['executive-votes-l']}
        </div>
        <div className='dip-list'>
          <div className='in-progress-executed'>
            <div className={inProgress ? 'in-progress check' : 'in-progress'} onClick={() => setInProgress(!inProgress)}>
              {lang['in-progress']}
              {inProgress ? <img src={check} alt='' /> : ''}
            </div>
            <div className={executed ? 'executed check' : 'executed'} onClick={() => setExecuted(!executed)}>
              FINISHED
              {executed ? <img src={check} alt='' /> : ''}
            </div>
          </div>
          <div className='dip-list-box'>
            {inProgress && <Link to='/dipfour'>
              <div className='dip-list-box-info'>
                <div className='dip-list-title'>
                  <div className='title-text'>
                    {lang['dip']}-004
                  </div>
                  <div className='dip-progress'>
                    <div className='progress-bg'></div>  ACTIVE
                  </div>
                </div>
                <div className='dip-list-des'>
                  Revote for changing the DERI burning mechanism
                </div>
                <div className='leading-option'>
                  <span>
                    {lang['leading-option']}

                    <span className='winner-option'>
                      {winnerOption ? winnerOption === "2" ? "Burn to mining vault (the status quo)" : "Burn to deadlock address" : ""}
                    </span>
                    {getLang('dip-leading-option', { value: (+winner).toFixed(0) })}
                  </span>
                </div>
                <div className='dip-time'>
                  2022 Sat. 4th June 4:00 AM UTC
                </div>
              </div>
            </Link>}
            {executed && <Link to='/dipthree'>
              <div className='dip-list-box-info'>
                <div className='dip-list-title'>
                  <div className='title-text'>
                    {lang['dip']}-003
                  </div>
                  <div className='dip-progress'>
                    <div className='executed-bg'></div>  FINISHED
                  </div>
                </div>
                <div className='dip-list-des'>
                  Vote for changing the DERI burning mechanism
                </div>
                <div className='leading-option'>
                  <span>
                    {lang['leading-option']}
                    <span className='winner-option'>
                      Burn to mining vault (the status quo)
                    </span>
                    {getLang('dip-leading-option', { value: 1454640 })}
                  </span>
                </div>
                <div className='dip-time'>
                  2022 Sat. 28th May 4:00 AM UTC
                </div>
              </div>
            </Link>}
            {executed && <><Link to='/diptwo'>
              <div className='dip-list-box-info'>
                <div className='dip-list-title'>
                  <div className='title-text'>
                    {lang['dip']}-002
                  </div>
                  <div className='dip-executed'>
                    <div className='executed-bg'></div>  FINISHED
                  </div>
                </div>
                <div className='dip-list-des'>
                  {lang['dip-two-des']}
                </div>
                <div className='leading-option'>
                  <span>
                    {lang['leading-option']}
                    <span className='winner-option'>
                      {lang['dip-two-winner']}
                    </span>
                    {getLang('dip-leading-option', { value: "1,440,582 " })}
                  </span>
                </div>
                <div className='dip-time'>
                  {lang['dip-two-time']}
                </div>
              </div>
            </Link>
              <Link to='/dipone'>
                <div className='dip-list-box-info'>
                  <div className='dip-list-title'>
                    <div className='title-text'>
                      {lang['dip']}-001
                    </div>
                    <div className='dip-executed'>
                      <div className='executed-bg'></div>  FINISHED
                    </div>
                  </div>
                  <div className='dip-list-des'>
                    {lang['dip-one-des']}
                  </div>
                  <div className='leading-option'>
                    <span>
                      {lang['leading-option']}
                      <span className='winner-option'>
                        {lang['dip-one-winner']}
                      </span>
                      {getLang('dip-leading-option', { value: "334,253" })}
                    </span>
                  </div>
                  <div className='dip-time'>
                    {lang['dip-one-time']}
                  </div>
                </div>
              </Link></>}
          </div>
        </div>
      </div>
    </div>
  )
}